import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classnames from "classnames";
import { useEffect, useState } from "react";
import { Controller, useForm } from "react-hook-form";
import { FormattedMessage, injectIntl } from "react-intl";
import "react-phone-number-input/style.css";
import { useDispatch, useSelector } from "react-redux";
import { Button, Form, FormGroup, Input, Label, Media, Row } from "reactstrap";
import validateOptions from "@constants/validate";
import { addCertificate } from "../store/action";
import { Editor } from "react-draft-wysiwyg";
import { convertToRaw, EditorState } from "draft-js";
import { toast } from "react-toastify";
import draftToHtml from "draftjs-to-html";
import { convertFileToBase64, uploadImage } from "../../../../../helper/common";
import { getProduct as getDataProduct } from "../store/action";
import { BG_BLANK } from "./../../../../../constants/app";
import avatarBlank from "../../../../../assets/images/avatars/bg-blank.png";
import { yupResolver } from "@hookform/resolvers/yup";
import Select from "react-select";
import * as yup from "yup";

const SidebarNewCertificate = ({
  open,
  toggleSidebar,
  intl,
  disable,
  avatar,
  setAvatar,
  setDisable,
}) => {
  const store = useSelector((state) => state.certificate);
  const { product } = useSelector((state) => state.certificate);
  const lang = useSelector((state) => state.common.language);
  const CertificateOptions = validateOptions.CertificateOptions;
  const dispatch = useDispatch();
  const cate = yup.object({
    productId: yup
      .number()
      .required(<FormattedMessage id="The course field is required" />),
    title: yup
      .string()
      .required(<FormattedMessage id="The title field is required" />),
    position: yup
      .string()
      .required(<FormattedMessage id="Position field is required" />),
    provider: yup
      .string()
      .required(<FormattedMessage id="Provider field is required" />),
  });
  const {
    register,
    formState: { errors },
    handleSubmit,
    control,
    setError,
    getValues,
    setValue,
  } = useForm({
    resolver: yupResolver(cate),
    mode: "all",
  });
  const [fileImage, setFileImage] = useState();
  const [valueContent, setValueContent] = useState(EditorState.createEmpty());
  const [changecertifycate, setChangecertifycate] = useState();
  // const { data } = useSelector((state) => state.products);
  // const product = useSelector((state) => state.products);
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  useEffect(() => {
    dispatch(
      getDataProduct({
        filter: {
          status: 1,
          lang,
          product_type: 0,
        },
        // skip: 0,
        // limit: 1000,
        order: [
          {
            key: "id",
            value: "asc",
          },
        ],
      })
    );
  }, [lang]);

  const onChange = async (e) => {
    const imageBase64 = await convertFileToBase64(e.target.files[0]);
    setFileImage(e.target.files[0]);
    setAvatar(imageBase64);
  };

  const onSubmit = async (values) => {
    const content =
      (valueContent &&
        draftToHtml(convertToRaw(valueContent?.getCurrentContent()))) ||
      null;
    setDisable(true);

    setDisable(true);
    var urlImage = " ";

    if (avatar) {
      const newData = avatar.replace(/,/gi, "").split("base64");
      if (newData[1]) {
        if (newData[1] == BG_BLANK) {
          toast.error(<FormattedMessage id="Image is required" />);
          setDisable(false);
          return;
        }
        const data = {
          imageData: newData[1],
          imageFormat: fileImage?.type?.split("/")[1] || "png",
        };

        await uploadImage(data, fileImage).then((res) => {
          urlImage = res?.data;
        });
      } else {
        urlImage = avatar;
      }

      if (urlImage) {
        dispatch(
          addCertificate({
            ...values,
            title: values.title || undefined,
            provider: values.provider || undefined,
            image: urlImage,
            productId: values?.productId || undefined,
            position: values.position || undefined,
            status: parseInt(values.status),
            type: String(values.type),
            content: content || " ",
            lang,
          })
        );
      }
    }
  };

  // const uploadCallback = async (file) => {
  //   var linkImg = avatarBlank;
  //   const imageBase64 = await convertFileToBase64(file);
  //   if (imageBase64) {
  //     const newData = imageBase64.replace(/,/gi, "").split("base64");
  //     if (newData[1]) {
  //       const data = {
  //         imageData: newData[1],
  //         imageFormat: file?.type?.split("/")[1] || "png",
  //       };
  //       await uploadImage(data, file).then((res) => {
  //         linkImg = res?.data;
  //       });
  //     }
  //   }
  //   return new Promise((resolve, reject) => {
  //     resolve({ data: { link: linkImg } });
  //   });
  // };

  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="Add new certificate" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for="certificate_title">
            <FormattedMessage id="certificate_title" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="title"
            id="title"
            placeholder=""
            innerRef={register(CertificateOptions.title)}
            onBlur={() => {
              let title = document.getElementById("title");
              if (title && title.value) {
                title.value = title.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["title"] })}
          />
          <small className="text-danger">
            {errors?.title && errors.title.message}
          </small>
          {errors?.title?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="Content">
            <FormattedMessage id="Content" />
          </Label>
          <Editor
            stripPastedStyles={true}
            toolbar={{
              options: ["inline", "textAlign"],
              inline: {
                inDropdown: false,
                options: ["bold", "italic", "underline"],
              },
            }}
            editorState={valueContent}
            onEditorStateChange={(data) => setValueContent(data)}
            name="content"
            innerRef={register}
          />
        </FormGroup>
        <FormGroup>
          <Label for="certificate_type">
            <FormattedMessage id="certificate_type" />
          </Label>
          <Input
            type="select"
            name="type"
            id="type"
            innerRef={register({ required: true })}
          >
            <option value="0">
              {intl.formatMessage({ id: "Professional" })}
            </option>
            <option value="1">{intl.formatMessage({ id: "Advantage" })}</option>

            <option value="2">{intl.formatMessage({ id: "Basic" })}</option>
            <option value="3">{intl.formatMessage({ id: "Pratice" })}</option>
          </Input>
        </FormGroup>
        <FormGroup>
          <Label for="Provider">
            <FormattedMessage id="Provider" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="provider"
            id="provider"
            placeholder=""
            innerRef={register(CertificateOptions.provider)}
            onBlur={() => {
              let provider = document.getElementById("provider");
              if (provider && provider.value) {
                provider.value = provider.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["provider"] })}
          />
          <small className="text-danger">
            {errors?.provider && errors.provider.message}
          </small>
          {errors?.provider?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label for="position">
            <FormattedMessage id="position" />{" "}
            <span className="text-danger">*</span>
          </Label>
          <Input
            name="position"
            id="position"
            placeholder=""
            innerRef={register(CertificateOptions.position)}
            onBlur={() => {
              let position = document.getElementById("position");
              if (position && position.value) {
                position.value = position.value.trim();
              }
            }}
            className={classnames({ "is-invalid": errors["position"] })}
          />
          <small className="text-danger">
            {errors?.position && errors.position.message}
          </small>
          {errors?.position?.type == "validate" && (
            <small className="text-danger">
              <FormattedMessage id="Invalid name" />
            </small>
          )}
        </FormGroup>
        <FormGroup>
          <Label>
            <FormattedMessage id="Course" />
            <span className="text-danger"> * </span>
          </Label>
          <Controller
            control={control}
            name="productId"
            render={({ field }) => {
              return (
                <Select
                  id="productId"
                  innerRef={register}
                  name="productId"
                  // placeholder={<FormattedMessage id="Select..." />}
                  className={classnames(
                    "react-select",
                    !changecertifycate
                      ? {
                          "is-invalid": errors["productId"],
                        }
                      : ""
                  )}
                  options={product?.map((item) => {
                    return {
                      value: item?.id && item?.id,
                      label: item.product_names[0].name || "",
                    };
                  })}
                  classNamePrefix="select"
                  {...field}
                  onChange={(e) => {
                    setError("productId", "");
                    setValue("productId", e?.value);
                    setChangecertifycate(e?.value);
                  }}
                />
              );
            }}
          ></Controller>
          <small className="text-danger">
            {errors?.productId && errors.productId.message}
          </small>
        </FormGroup>
        <FormGroup>
          <Label for="status">
            <FormattedMessage id="status" />{" "}
          </Label>
          <Input
            type="select"
            name="status"
            id="status"
            innerRef={register({ required: true })}
          >
            <option value="1">{intl.formatMessage({ id: "Active" })}</option>

            <option value="0">{intl.formatMessage({ id: "Blocked" })}</option>
          </Input>
        </FormGroup>
        <FormGroup>
          <Label for="Image">
            <FormattedMessage id="Image" />
            <span className="text-danger"> *</span>
          </Label>
          <Row className="align-items-end p-1">
            <Media className="mr-25" left>
              <Media
                object
                className="rounded mr-50"
                src={avatar}
                height="100"
                width="100"
              />
            </Media>{" "}
            <Row className="flex-column px-1">
              <Button.Ripple tag={Label} className="mt-1" color="primary">
                <FormattedMessage id="Upload" />
                <Input
                  type="file"
                  onChange={(e) => onChange(e)}
                  hidden
                  accept="image/*"
                />
              </Button.Ripple>
            </Row>
          </Row>
        </FormGroup>
        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="Save" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default injectIntl(SidebarNewCertificate);
